body {
  background-image: url('backgroung.jpg');
  background-size: cover;
}

.productDiv{
  margin-top: 3%;
  height: fit-content;
  width: 300px;
  border: 5px solid black;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

p {
  margin: 0px;
}

span {
  text-align: center;
  font-size: x-large;
}

.wrapper.shift-left .roomsDiv {
  margin-left: 0;
  margin-right: auto;
}


@keyframes color-change {
  from {
    color: #ff0000;
  }

  to {
    color: #00ff00;
  }
}

@keyframes color-change {
  from {
    color: #ff0000;
  }

  to {
    color: #00ff00;
  }
}

input[type="text"],
input[type="number"],
select,
textarea {
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  width: 70%;
}

label {
  display: block;
  margin-bottom: 10px;
  font-size: 18px;
}

.error-message {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}

button.addProductBtn {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #007bff;
  color: white;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

button.addProductBtn:hover {
  background-color: #0069d9;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.4);
  transform: scale(1.05);
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 255, 0.5);
}

h1 {
  text-align: center;
}

.outDiv {
  height: fit-content;
  width: 350px;
  border: 5px solid black;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

p {
  margin: 0px;
}

span {
  text-align: center;
  font-size: x-large;
}

.roomsDiv,
#roomTypeDiv {
  width: fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: space-between;
  border-radius: 50%;
}

.addRoomBtn {
  position: relative;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  margin: 5px;
  border: 5px solid black;
  font-size: x-large;
}

.deleteRoomBtn {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: red;
  color: white;
  padding: 5px;
  font-size: small;
  cursor: pointer;
}

.wrapper.shift-left .roomsDiv {
  margin-left: 0;
  margin-right: auto;
}

#headLine {
