.addProductForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.addProductForm label {
  margin: 10px 0;
}

.addProductForm input[type="text"],
.addProductForm select,
.addProductForm textarea {
