.productListDiv{
  display: flex;
  border: 0px;
  flex-wrap: wrap;
  flex-direction: row;
}

.roomDiv{
  position: relative;
  display: flex;
  flex-direction: column;
}

.deletBTN {
  background: none;
  color: #ff0000;
  font-size: medium;
  font-weight: bolder;
  box-shadow: none;
  padding: 0%;
  margin: 0%;
  text-align: right;
}
